@import 'core/variables';
@import 'core/reset';

.projects__list--item {
  background-color: $linenwhite;
  box-shadow: 1px 0px 1px 2px $shadowgrey;
  min-width: 200px;
  min-height: 275px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  &:hover {
    transform: scale(1.03);
    background: rgb(250, 240, 230);
    background: linear-gradient(
      0deg,
      rgba(250, 240, 230, 1) 0%,
      rgba(164, 180, 205, 1) 100%
    );
    box-shadow: 1px 0px 5px 2px $shadowgrey;
  }
  &__title {
    margin: 3vh 0 3vh 0;
    font-size: 0.6em;
  }
  &__img {
    width: 85%;
  }
  &__text {
    font-size: 0.4em;
    margin: 1rem;
    text-align: center;
    line-height: 1.2;
  }
}

.projects__linknav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-size: 0.7em;
  &--link {
    color: $bcgblue;
    margin: 1rem;
  }
}
