@import 'core/variables';
@import 'core/reset';
.aboutme {
  height: 100vh;
  padding-top: 8vh;
  margin: 0 5vw 0 5vw;
  &__title {
    font-size: 3rem;
    margin: 5vh 0 5vh 0;
  }
  &__image {
    width: 100px;
    height: 100px;
    margin-right: 10vw;
    box-shadow: inset 0px 0px 300px 300px rgba(133, 159, 196, 0.6);
    clip-path: circle();
    shape-outside: circle();
    float: left;
    background-image: url(../images/1648032498973.jpg);
    background-position: center;
    background-size: cover;
    background-blend-mode: saturation;
    @media (min-width: 768px){
      width: 400px;
      height: 400px;
    }
  }
  &__article {
    font-size: 12px;
    margin: 3vh 0 3vh 0;

    @media (min-width: 768px) {
      font-size: 18px;
      //margin: 30px;
    }
    @media (min-width: 1024px) {
      font-size: 20px;
      line-height: 1.5rem;
    }
  }
  &__btn {
    min-width: 120px;
    height: 30px;
    font-family: $mainfont;
    font-size: 16px;
    font-weight: bold;
    color: $linenwhite;
    background-color: $bcgblue;
    border: $htmlblue;
    border-radius: 15px;
    @media (min-width: 768px) {
      margin-left: 25%;
    }
   
    &:hover {
      background-color: $hoverblue;
    }
  }
}
